
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  overflow: hidden;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.stars,
.twinkling,
.clouds {
	position: absolute;
	display: block;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	width: 100%;
	height: 100%;
}

.stars {
	z-index: 0;
	background: #000 url("https://i.ibb.co/g91MwKZ/stars.png") repeat top center;
}

.twinkling {
	z-index: 1;
	background: transparent url("https://i.ibb.co/pnJQm6V/twinkling.png") repeat
		top center;
	animation: move-twink-back 200s linear infinite;
}

.clouds {
	z-index: 2;
	background: transparent url("https://i.ibb.co/BsG8rYZ/clouds.png") repeat top
		center;
	animation: move-clouds-back 200s  infinite;
	top: 10%;
}




















@keyframes move-twink-back {
	from {
		background-position: 0 0;
	}
	to {
		background-position: -10000px 5000px;
	}
}

@keyframes move-clouds-back {
	from {
		background-position: 0 0;
	}
	to {
		background-position: 10000px 0;
	}
}
